.enter {
  opacity: 0;
  transform: scaleY(0);
}

.enter-active {
  opacity: 1;
  transform: scaleY(1);
  transition: transform 300ms cubic-bezier(0.23, 0.1, 0.32, 1) 100ms,
    opacity 300ms cubic-bezier(0.23, 0.1, 0.32, 1) 100ms;
  transform-origin: center top;
}

.exit {
  opacity: 1;
}

.exit-active {
  opacity: 0;
  transform: scaleY(0);
  transition: transform 300ms cubic-bezier(0.23, 0.1, 0.32, 1) 100ms,
    opacity 300ms cubic-bezier(0.23, 0.1, 0.32, 1) 100ms;
  transform-origin: center top;
}
